
showMore();
function showMore() {	  
	const btnAdds = document.querySelectorAll('.--btn-add');
	btnAdds.forEach(btnAdd=> {
		btnAdd.addEventListener('click', function(e)  {
			this.parentElement.classList.remove('--hide-content');			
			btnAdd.remove();
		})
	})
}

if(document.querySelectorAll('.accordion')){accordion()}
function accordion() {
	const accordions = document.querySelectorAll('.accordion__trigger');
	accordions.forEach(accordion => {
		accordion.addEventListener('click', (e) => {
			const self = e.currentTarget;
			const parent = self.parentElement;
			const content = parent.querySelector('.accordion__content');
			parent.classList.toggle('open');
			
			if (parent.classList.contains('open')) {
				accordion.setAttribute('aria-expanded', true);
				content.setAttribute('aria-hidden', false);
				
			} else {
				accordion.setAttribute('aria-expanded', false);
				content.setAttribute('aria-hidden', true);
				
			}
		});			
	});		
	
}

