if(document.querySelector('.goods-card__slide')){
  changeIMG()
}
function changeIMG() {
  const image=['./img/products/3.jpg','./img/products/5.jpg','./img/products/7.jpg']
  const slider =['./img/products/13.jpg','./img/products/14.jpg','./img/products/15.jpg','./img/products/16.jpg']
  
  btnadd('.goods-card__color-1', '.goods-card__img>img', 'click', image[0]);
  btnadd('.goods-card__color-2', '.goods-card__img>img', 'click', image[1]);
  btnadd('.goods-card__color-3', '.goods-card__img>img', 'click', image[2]);


  btnadd('.goods-card__slide-1', '.goods-card__img>img', 'mouseover', slider[0]);
  btnadd('.goods-card__slide-2', '.goods-card__img>img', 'mouseover', slider[1]);
  btnadd('.goods-card__slide-3', '.goods-card__img>img', 'mouseover', slider[2]);
  btnadd('.goods-card__slide-4', '.goods-card__img>img', 'mouseover', slider[3]);

  function btnadd(btn,img, event, way) {
    document.querySelector(btn).addEventListener(event, () => {
        document.querySelector(img).src= way;
      })
    }
}





